import React from "react";
import { __ } from "@wordpress/i18n";
import Settings from "./block.json";
import {
  PanelBody,
  ToggleControl,
  FormTokenField,
  Button,
  CheckboxControl,
  TextControl,
} from "@wordpress/components";
import { resetAttributes } from "../utils";
import { InspectorControls } from "@wordpress/block-editor";

export default function Inspecteur({
  attributes,
  setAttributes,
  customPostTypes,
  nestedCategories,
}) {
  const {
    excludedCPTItems,
    excludedCategories,
    excludedArticles,
    hasCPT,
    openInNewTab,
    anchor,
    showPosts,
  } = attributes;

  // Initialisation de 'hasCPT' avec 'page' et 'Categorie des articles' à true par défaut
  React.useEffect(() => {
    let updated = false;
    let newHasCPT = { ...hasCPT };

    if (newHasCPT["page"] === undefined) {
      newHasCPT["page"] = true;
      updated = true;
    }

    if (newHasCPT["categorie"] === undefined) {
      newHasCPT["categorie"] = true;
      updated = true;
    }

    if (updated) {
      setAttributes({ hasCPT: newHasCPT });
    }
  }, []);
  // ajout de la clé anchor
  const handleAnchorChange = (newAnchor) => {
    setAttributes({ anchor: newAnchor });
  };

  // Assigne les ids par rapport aus noms d'articles
  const createArticleNameToIdMap = (nestedCategories) => {
    const map = {};

    const addArticlesToMap = (categories) => {
      categories.forEach((category) => {
        category.posts.forEach((post) => {
          map[post.title.rendered] = post.id;
        });

        if (category.children && category.children.length > 0) {
          addArticlesToMap(category.children);
        }
      });
    };

    addArticlesToMap(Object.values(nestedCategories));
    return map;
  };

  const articleNameToIdMap = createArticleNameToIdMap(nestedCategories);

  /**
   * Assigne les ids par rapport aus noms des customs post types ou catégories
   */
  function createNameToIdMap(categories, type) {
    const map = {};
    const addItemsToMap = (categories) => {
      categories.forEach((category) => {
        if (type === "category") {
          map[category.name] = category.id;
        } else {
          category?.posts?.forEach((post) => {
            map[post.title.rendered] = post.id;
          });
        }
        if (category.children && category.children.length > 0) {
          addItemsToMap(category.children);
        }
      });
    };
    addItemsToMap(Object.values(categories));
    return map;
  }

  // Assigne les ids par rapport aus noms des catégories
  const categoryNameToIdMap = createNameToIdMap(nestedCategories, "category");

  // Met à jours la liste des catégories à exclure
  const updateExcludedCategories = (categoryNames) => {
    const updatedExcludedCategories = categoryNames.reduce(
      (acc, categoryName) => {
        const categoryId = categoryNameToIdMap[categoryName];
        if (categoryId) {
          acc[categoryId] = true;
        }
        return acc;
      },
      {}
    );

    setAttributes({ excludedCategories: updatedExcludedCategories });
  };

  // Affiche isChecked/unChecked par post type
  const handleCptCheckbox = (type, isChecked) => {
    setAttributes({
      ...attributes,
      hasCPT: {
        ...hasCPT,
        [type]: isChecked,
      },
    });
  };

  // Affiche les articles isChecked/unChecked
  const togglePostsDisplay = () => {
    console.log("showpost", showPosts);
    setAttributes({ showPosts: !showPosts });
  };

  // Capitalise la première lettre de chaque titre
  const capitalizeFirstLetter = (string) =>
    string.charAt(0).toUpperCase() + string.slice(1).toLowerCase();

  // Affichage des titres selon le type de post
  const postTypeTitleDisplay = (type) => {
    if (type === "post") {
      return __(" Catégories des Articles", "ocade-blocks");
    } else if (type === "page") {
      return __("Pages", "ocade-blocks");
    } else {
      return __(capitalizeFirstLetter(type), "ocade-blocks");
    }
  };

  // Récupération de la liste de toutes les catégories
  const getAllCategories = (categories) => {
    const allCategories = [];

    const addCategories = (categories) => {
      categories.forEach((category) => {
        allCategories.push(category.name);

        if (category.children && category.children.length > 0) {
          addCategories(category.children);
        }
      });
    };

    addCategories(Object.values(categories));
    return allCategories;
  };

  // Récupération de la liste de tout les articles
  const getAllArticles = (categories) => {
    const allArticles = [];

    const addArticles = (categories) => {
      categories.forEach((category) => {
        category.posts.forEach((post) => {
          allArticles.push(post.title.rendered);
        });

        if (category.children && category.children.length > 0) {
          addArticles(category.children);
        }
      });
    };

    addArticles(Object.values(categories));
    return allArticles;
  };

  // Assigne les ids par rapport aus noms des customs post types
  const createCPTTitleToIdMap = (customPostTypes) => {
    const titleToIdMap = {};
    Object.keys(customPostTypes).forEach((type) => {
      customPostTypes[type].forEach((item) => {
        titleToIdMap[item.title.rendered] = item.id;
      });
    });
    return titleToIdMap;
  };

  const cptTitleToIdMap = createCPTTitleToIdMap(customPostTypes);

  // Met à jours la liste des customs post type à exclure
  const updateExcludedCPTItems = (type, selectedTitles) => {
    const updatedExclusions = selectedTitles.reduce((acc, title) => {
      const itemId = cptTitleToIdMap[title];
      if (itemId) {
        acc[itemId] = true;
      }
      return acc;
    }, {});

    setAttributes({
      ...attributes,
      excludedCPTItems: {
        ...excludedCPTItems,
        [type]: updatedExclusions,
      },
    });
  };

  const updateExcludedArticles = (articleTitles) => {
    const updatedExcludedArticles = articleTitles.reduce((acc, title) => {
      const articleId = articleNameToIdMap[title];
      if (articleId) {
        acc[articleId] = true;
      }
      return acc;
    }, {});

    setAttributes({ excludedArticles: updatedExcludedArticles });
  };

  return (
    <InspectorControls>
      <Button
        variant="primary"
        style={{
          display: "flex",
          justifyContent: "center",
          width: "80%",
          margin: "0 auto .65rem",
        }}
        title="Remettre à zéro les paramètres du bloc ?"
        onClick={() => {
          setAttributes(resetAttributes(Settings));
        }}
      >
        Réinitialiser le bloc
      </Button>

      <PanelBody title={__("Paramètres d'affichage", "ocade-blocks")}>
        {Object.keys(customPostTypes).map((type) => (
          <CheckboxControl
            key={type}
            label={postTypeTitleDisplay(type)}
            checked={hasCPT[type] || false}
            onChange={(isChecked) => handleCptCheckbox(type, isChecked)}
          />
        ))}

        <CheckboxControl
          label={__("Articles", "text-domain")}
          checked={showPosts}
          onChange={togglePostsDisplay}
        />
        <ToggleControl
          label={__("Ouverture des liens", "ocade-blocks")}
          checked={openInNewTab}
          onChange={() => setAttributes({ openInNewTab: !openInNewTab })}
          help={
            openInNewTab
              ? __("Nouvel onglet", "ocade-blocks")
              : __("Même onglet", "ocade-blocks")
          }
        />
      </PanelBody>

      <PanelBody
        title={__("Exclure des Catégories", "ocade-blocks")}
        initialOpen={false}
      >
        <FormTokenField
          value={Object.keys(excludedCategories).map((id) => {
            return Object.keys(categoryNameToIdMap).find(
              (name) => categoryNameToIdMap[name] === parseInt(id)
            );
          })}
          suggestions={getAllCategories(nestedCategories)}
          onChange={updateExcludedCategories}
          __experimentalExpandOnFocus={true}
          placeholder={__("Ajoutez des catégories à exclure", "ocade-blocks")}
        />
      </PanelBody>
      {Object.keys(customPostTypes).map((type, index) => (
        <PanelBody
          key={index}
          title={__(
            "Exclure des " + postTypeTitleDisplay(type),
            "ocade-blocks"
          )}
          initialOpen={false}
        >
          <FormTokenField
            value={
              type === "post"
                ? Object.keys(excludedArticles).map((id) => {
                    return Object.keys(articleNameToIdMap).find(
                      (title) => articleNameToIdMap[title] === parseInt(id)
                    );
                  })
                : Object.keys(excludedCPTItems[type] || {}).map((id) => {
                    return Object.keys(cptTitleToIdMap).find(
                      (title) => cptTitleToIdMap[title] === parseInt(id)
                    );
                  })
            }
            suggestions={
              type === "post"
                ? getAllArticles(nestedCategories)
                : customPostTypes[type].map((item) => item.title.rendered)
            }
            onChange={(selectedTitles) =>
              type === "post"
                ? updateExcludedArticles(selectedTitles)
                : updateExcludedCPTItems(type, selectedTitles)
            }
            __experimentalExpandOnFocus={true}
            placeholder={__("Que voulez-vous exclure ?", "ocade-blocks")}
          />
        </PanelBody>
      ))}

      <PanelBody title={__("Paramètres Avancés", "ocade-blocks")}>
        <TextControl
          label={__("ID d'Ancre", "ocade-blocks")}
          value={anchor || ""}
          onChange={handleAnchorChange}
          help={__(
            "Ajoutez un ID unique pour cibler ce bloc avec un lien d'ancre.",
            "ocade-blocks"
          )}
        />
      </PanelBody>
    </InspectorControls>
  );
}
