import React from 'react';
import {__} from '@wordpress/i18n';
import {useBlockProps} from '@wordpress/block-editor';

/**
 * Render the block in the editor.
 */
export default function Block({attributes, customPostTypes, nestedCategories}) {
  const {excludedCPTItems, excludedCategories, openInNewTab, hasCPT, excludedArticles, showPosts} = attributes;

  const isCategoryExcluded = (category) => {
    return excludedCategories[category.id];
  };

  // Template pour catégories liées à un article
  const renderCategoryItems = (categories) => {
    return (
      <ul className='category-list'>
        {Object.values(categories).map((category) => {
          if (isCategoryExcluded(category)) {
            return null;
          }

          return (
            <li
              className='category-item'
              key={category.id}
            >
              <h3 className='category-title'>{category.name}</h3>
              {/* Check the showPosts attribute before rendering posts */}
              {showPosts && category.posts && category.posts.length > 0 && (
                <ul className='posts-list'>
                  {category.posts
                    .filter((post) => !excludedArticles[post.id])
                    .map((post) => (
                      <li
                        className='post-item'
                        key={post.id}
                      >
                        <a
                          href={post.link}
                          target='_blank'
                          rel='noopener noreferrer'
                        >
                          {post.title.rendered}
                        </a>
                      </li>
                    ))}
                </ul>
              )}
              {category.children && category.children.length > 0 && renderCategoryItems(category.children)}
            </li>
          );
        })}
      </ul>
    );
  };

  // Determine si l'élement contient la clé parent qui est > 0
  const getItemStyle = (item) => {
    return item.parent > 0 ? {marginLeft: '20px'} : {};
  };

  // Vérifie si un élément CPT est exclu
  const isItemSelected = (item, type) => {
    return !!excludedCPTItems[type]?.[item.id];
  };

  // Organise les données des CPT en structure hiérarchique
  const organizeCPTData = () => {
    const organizedData = {};

    Object.keys(customPostTypes).forEach((cptType) => {
      const items = Array.isArray(customPostTypes[cptType]) ? customPostTypes[cptType] : [];
      const hierarchy = items.reduce((acc, item) => {
        if (!isItemSelected(item, cptType)) {
          acc[item.id] = {...item, children: []};
        }
        return acc;
      }, {});

      items.forEach((item) => {
        if (hierarchy[item.id] && item.parent && hierarchy[item.parent]) {
          hierarchy[item.parent].children.push(hierarchy[item.id]);
        }
      });

      organizedData[cptType] = Object.values(hierarchy).filter((item) => !item.parent);
    });

    return organizedData;
  };

  const organizedCPTContents = organizeCPTData();

  // Rendu récursif des éléments CPT
  const renderCPTItems = (items, cptType) =>
    items.map((item, index) => {
      if (isItemSelected(item, cptType)) return null;

      return (
        <li
          key={index}
          className={item.parent === 0 ? 'cpt-header' : 'cpt-item'}
          style={getItemStyle(item)}
        >
          {item.parent === 0 ? (
            <h2 className='cpt-title'>
              <a
                href={item.link}
                target={openInNewTab ? '_blank' : '_self'}
                rel={openInNewTab ? 'noopener noreferrer' : ''}
              >
                {item.title.rendered}
              </a>
            </h2>
          ) : (
            <a
              href={item.link}
              target={openInNewTab ? '_blank' : '_self'}
              rel={openInNewTab ? 'noopener noreferrer' : ''}
            >
              {item.title.rendered}
            </a>
          )}
          {item.children && item.children.length > 0 && <ul className='children-list'>{renderCPTItems(item.children, cptType)}</ul>}
        </li>
      );
    });

  return (
    <div {...useBlockProps()}>
      <div className='cpt-contents'>
        {Object.keys(organizedCPTContents).map(
          (cptType) =>
            hasCPT[cptType] && (
              <div
                key={cptType}
                className={`cpt-section cpt-section-${cptType}`}
              >
                <h3 className='cpt-section-title'>{__(cptType.charAt(0).toUpperCase() + cptType.slice(1), 'ocade-blocks')}</h3>
                <ul className='cpt-items-list'>{cptType === 'post' ? renderCategoryItems(Object.values(nestedCategories)) : renderCPTItems(organizedCPTContents[cptType], cptType)}</ul>
              </div>
            ),
        )}
      </div>
    </div>
  );
}
